// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'
import SectionTitle from '@/components/shack/SectionTitle.vue'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    SectionTitle
  },
  async asyncData({ store }) {
    const space = store.state.space.currentSpace
    return {
      ...space
    }
  },
  data() {
    return {
      loading: false
    }
  },
  computed: {
    ...mapState({
      space: state => state.space.currentSpace
    })
  },
  methods: {
    updateSpace() {
      const {
        name,
        description,
        unit,
        currency,
        currency_symbol,
        tax_rate,
        payment_instructions,
        timezone,
        timezone_name,
        wifi_name,
        wifi_password,
        capacity,
        in_eu,
        address,
        city,
        zipcode,
        country,
        neighborhood,
        latitude,
        longitude,
        id
      } = this

      const payload = {
        name,
        description,
        unit,
        currency,
        currency_symbol,
        tax_rate,
        payment_instructions,
        timezone,
        timezone_name,
        wifi_name,
        wifi_password,
        capacity,
        in_eu: Boolean(in_eu),
        address,
        city,
        zipcode,
        country,
        neighborhood
      }

      if (latitude) payload[latitude] = latitude
      if (longitude) payload[longitude] = longitude

      this.loading = !this.loading

      this.$space
        .updateOneSpace(id, payload)
        .then(({ data }) => {
          this.$store.commit('space/setSpace', data)

          this.$bvToast.toast(`Updated ${name} successfully`, {
            title: 'Success',
            variant: 'success',
            solid: true
          })

          this.loading = !this.loading
          setTimeout(() => {
            this.$router.push({ name: 'space' })
          }, 3000)
        })
        .catch(err => {
          const message = err.response
            ? JSON.stringify(err.response.data.errors)
            : err.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
          this.loading = !this.loading
        })
    }
  }
}
